/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Logrinto Blog","subtitle":"Blog zum Interaction Designer an der Schule für Gestaltung Zürich","author":{"name":"Logrinto","twitter":"https://twitter.com/logrinto"}}},"markdownRemark":{"id":"/home/travis/build/logrinto/website/src/pages/articles/2017-11-11—CaseStudy-SBB/index.md absPath of file >>> MarkdownRemark","html":"<h2>Ausgangslage – Konventionelle Anzeige</h2>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to8\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/20171111_084024-4154288c13952b0da654e376b41fd6f8-84fcc.jpg\" srcset=\"/static/20171111_084024-4154288c13952b0da654e376b41fd6f8-5424a.jpg 750w,\n/static/20171111_084024-4154288c13952b0da654e376b41fd6f8-6baeb.jpg 1500w,\n/static/20171111_084024-4154288c13952b0da654e376b41fd6f8-84fcc.jpg 3000w,\n/static/20171111_084024-4154288c13952b0da654e376b41fd6f8-4b69e.jpg 4032w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n    \t<figcaption>Abfahrtszeiten als Plakate</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-9to12\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/20171111_083648-e8a6706001d0242368388d71eca99aa8-84fcc.jpg\" srcset=\"/static/20171111_083648-e8a6706001d0242368388d71eca99aa8-5424a.jpg 750w,\n/static/20171111_083648-e8a6706001d0242368388d71eca99aa8-6baeb.jpg 1500w,\n/static/20171111_083648-e8a6706001d0242368388d71eca99aa8-84fcc.jpg 3000w,\n/static/20171111_083648-e8a6706001d0242368388d71eca99aa8-4b69e.jpg 4032w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Abfahrtszeiten als Screen ohne Interaktion</figcaption>\n    </figure>\n  </div>\n</div>\n<p>Bis anhin wurden die Abfahrtszeiten an mehreren Orten innerhalb des Hauptbahnhof angezeigt. Nicht nur auf den Gleisen und in Gleisnähe dienen die gelb markierten Plakate oder Screens zur Orientierung, sondern es gibt auch an vielen Stellen im Hauptbahnhof diese Information zum ansehen ohne dass eine Interaktion über Berührung getätigt werden muss. Die Reisenden informieren sich und können dann auf ihr jeweiliges Gleis – dem Orientierungssystem folgend – gehen. Die Gestaltung ist klar und funktional und dem Lesenden dienend.</p>\n<h2>Ausgangslage – Neues Panel</h2>\n<div class>\n  <figure>\n  \t\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 1920px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.25%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/handling-6802092d51ddd7c4c9b9b09d6c3db500-fa8a7.jpg\" srcset=\"/static/handling-6802092d51ddd7c4c9b9b09d6c3db500-b1a9c.jpg 750w,\n/static/handling-6802092d51ddd7c4c9b9b09d6c3db500-47ed8.jpg 1500w,\n/static/handling-6802092d51ddd7c4c9b9b09d6c3db500-fa8a7.jpg 1920w\" sizes=\"(max-width: 1920px) 100vw, 1920px\">\n    </span>\n  </span>\n  \n  \t<figcaption>Fahrplanauskunft als Hauptfunktion des neuen Panels</figcaption>\n  </figure>\n</div>\n<p>Seit Herbst 2017 steht im stark frequentierten Hauptbahnhof Zürich im Bereich des S-Bahnhofes ein Panel, welches mit einem Touch-Screen bedient werden kann. Reisende können hier Informationen zum Schienensystem, den Tarifzonen und den Abfahrten der Züge erhalten.\nDie Anzeige erfolgt mittels LCD-Display und die Hardware scheint auf der Höhe der Zeit zu sein.</p>\n<h2>Analyse zum Gebrauch</h2>\n<p>In der Klasse wurden nachfolgende Bedürfnisse zusammengetragen. Diese lassen sich beliebig erweitern, jedoch beschränken wir uns bei der Neugestaltung auf die hier abgebildeten.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to6\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 1355px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 74.53874538745387%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_3921-b9115c45128894856534493d48fdf96d-cbc43.jpg\" srcset=\"/static/IMG_3921-b9115c45128894856534493d48fdf96d-6054a.jpg 750w,\n/static/IMG_3921-b9115c45128894856534493d48fdf96d-cbc43.jpg 1355w\" sizes=\"(max-width: 1355px) 100vw, 1355px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n  <div class=\"col-7to12\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_9672-dc5283621593410b34dff92d8166337b-84fcc.jpg\" srcset=\"/static/IMG_9672-dc5283621593410b34dff92d8166337b-5424a.jpg 750w,\n/static/IMG_9672-dc5283621593410b34dff92d8166337b-6baeb.jpg 1500w,\n/static/IMG_9672-dc5283621593410b34dff92d8166337b-84fcc.jpg 3000w,\n/static/IMG_9672-dc5283621593410b34dff92d8166337b-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n</div>\n<div class=\"grid\">\n  <div class=\"col-1to4\">\n    <h4>Zielgruppen/Persona</h4>\n    <ul>\n      <li>Personen mit Informationsbedürfins</li>\n      <li>Personen ohne Smartphone oder dem Wissen um Online-Angebote</li>\n      <li>Ältere Touristen</li>\n      <li>Möchten Information innert 10–30 Sekunden</li>\n      <li>Personen, die eine Fart antreten möchten</li>\n      <li>Personen können Lesen, sind aber eventuell seh- oder bewegungsbeeinträchtigt</li>\n      <li>Verschiedene Personen haben verschiedene Ansprüche an die Informationstiefe</li>\n    </ul>\n  </div>\n  <div class=\"col-5to8\">\n    <h4>Informationsbedarf</h4>\n    <ul>\n      <li>Abfartszeiten</li>\n      <li>Gleis</li>\n      <li>Streckenverlauf</li>\n      <li>Dauer</li>\n      <li>Linie (Name)</li>\n      <li>Einschränkungen im Schienensystem</li>\n      <li>Verpflegung/Restaurant</li>\n      <li>Wo sind die Wagen der 1. oder 2. Klasse</li>\n    </ul>\n  </div>\n</div>\n<h2>Analyse zur Gestaltung</h2>\n<p>Die nachfolgende Analyse und die daraus erarbeiteten Vorschläge der Studenten beziehen sich lediglich auf den Teil der Abfahrtszeiten. Da es sich dabei wohl um einer der am häufigst benutzten Teile handeln dürfe, kann eine Interaktion von Vorteil sein.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to7\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-84fcc.jpg\" srcset=\"/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-5424a.jpg 750w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-6baeb.jpg 1500w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-84fcc.jpg 3000w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Die Schriftgr&#xF6;sse auf dem Panel ist zu klein f&#xFC;r die Aufl&#xF6;sung des Screens und kann kaum gelesen werden. Mit einer &#xAB;Lupe&#xBB; kann man sich einzelne Teile vergr&#xF6;ssert darstellen. </figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2481px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 141.39459895203547%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/poster-fc67d8542234e26b7d47403d005dea9b-09308.jpg\" srcset=\"/static/poster-fc67d8542234e26b7d47403d005dea9b-33bd6.jpg 750w,\n/static/poster-fc67d8542234e26b7d47403d005dea9b-00c59.jpg 1500w,\n/static/poster-fc67d8542234e26b7d47403d005dea9b-09308.jpg 2481w\" sizes=\"(max-width: 2481px) 100vw, 2481px\">\n    </span>\n  </span>\n  \n      <figcaption>Das Poster (hier in der Abbildung) diente ganz offensichtlich dem Screendesign als Vorlage</figcaption>\n    </figure>\n  </div>\n</div>\n<p>Die Gestaltung des Panels wurde ganz offensichtlich dem Poster der gedruckten Version angepasst oder übernommen. Wo in der gedruckten Version mit der hohen Auflösung die Schrift noch klar lesbar ist, versagt der niedrig aufgelöste Screen und die Schrift wird fast unlesbar. Die Abfahrttabelle ist auf mehere «Seiten» verteilt was ebenso 1:1 dem Poster entnommen wurde. Eine «Lupe» soll dieses Defizit ausbessern, wirkt aber Fremd und macht das Auffinden seiner Zugsverbindung noch schwieriger.</p>\n<h2>Analyse zum Interface</h2>\n<p>Das Userinterface für die momentane Fahrplanauskunft besteht aus den folgenden Elementen:</p>\n<ul>\n<li>Blättern – vorwärts und zurück</li>\n<li>Lupe – ein und aus</li>\n<li>«Rollstuhl-Modus» – ein und aus</li>\n<li>Zurück auf Homescreen</li>\n</ul>\n<h4>Blättern</h4>\n<p>Das Blättern zwischen den drei Screens ist eher schwerfällig. Es muss relativ stark gedrückt werden und der Touch/Swipe wird nicht immer erkannt.</p>\n<h4>Lupe</h4>\n<p>Die Lupe verdeckt durch ihre eigene Grösse sehr viel Text. Eine Navigation innerhalb des Screens ist schwierig, da man den Finger bewegen muss und dabei die Lupe beobachten kann, nicht aber den Finger gezielt an eine Position bewegen, da die gesuchte Zeit durch die Lube eventuell verdeckt wurde.</p>\n<h4>Rollstuhl-Modus</h4>\n<p>Die Anordnung der Abfahrtszeiten zieht sich über ⅔ der Höhe des Screens dadurch wird es für Rollstuhlfahrende und kleine Personen schwierig die oberen Teile des Screens zu lesen. Da in der Gestaltung auf dies keine Rücksicht genommen wurde, wird mit einem Touch auf ein Rollstuhl-Icon der Screen herunter gefahren. Für ältere Personen, die am unteren Rand lesen möchten, sich aber nicht mehr Bücken können, gibt es keine Hilfe um die Gestaltung hoch zu stellen.</p>\n<div class=\"grid\">\n  <div class=\"col-1to4\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-84fcc.jpg\" srcset=\"/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-5424a.jpg 750w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-6baeb.jpg 1500w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-84fcc.jpg 3000w,\n/static/panel_lupe_IMG_2161-d6cfdaf03259794ad79b73f02fbd0185-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Normale Ansicht</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-5to8\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/panel_IMG_0980-25c23481abf21533c60b5c730f9d6244-84fcc.jpg\" srcset=\"/static/panel_IMG_0980-25c23481abf21533c60b5c730f9d6244-5424a.jpg 750w,\n/static/panel_IMG_0980-25c23481abf21533c60b5c730f9d6244-6baeb.jpg 1500w,\n/static/panel_IMG_0980-25c23481abf21533c60b5c730f9d6244-84fcc.jpg 3000w,\n/static/panel_IMG_0980-25c23481abf21533c60b5c730f9d6244-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>&#xAB;Rollstuhl-Modus&#xBB;</figcaption>\n    </figure>\n  </div>\n</div>\n<h4>Zurück/Home</h4>\n<p>Der Home-Button ist rot unterlegt und mit einem Haus markiert. Die Symbolik und die positionierung scheinen in Ordnung.</p>\n<br>\n<br>\n<br>\n<h2>Redesign</h2>\n<p>Die Betrachtung vor Ort, Analyse und das Redesign des Panels erfolgte innert 8 Schullektionen. Somit blieb für die einzelnen Schritte nicht viel Zeit. Nachdem in der Klasse die Analyse des bestehenden Panels erfolgte, wurde folgender Zeitplan für den Rest des Tages eingehalten:</p>\n<ul>\n<li>Paper-Prototype (1h)</li>\n<li>Austausch und Präsentation in Klasse (45min)</li>\n<li>Entwurf (2:15h)</li>\n<li>Präsentation in der Klasse (1h)</li>\n</ul>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to3\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_2800-0043512858c747808a2328a76040230e-84fcc.jpg\" srcset=\"/static/IMG_2800-0043512858c747808a2328a76040230e-5424a.jpg 750w,\n/static/IMG_2800-0043512858c747808a2328a76040230e-6baeb.jpg 1500w,\n/static/IMG_2800-0043512858c747808a2328a76040230e-84fcc.jpg 3000w,\n/static/IMG_2800-0043512858c747808a2328a76040230e-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n  <div class=\"col-4to6\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2544px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_3396-369216b758c0815500af50dc3c15e73d-2b8b3.jpg\" srcset=\"/static/IMG_3396-369216b758c0815500af50dc3c15e73d-355b3.jpg 750w,\n/static/IMG_3396-369216b758c0815500af50dc3c15e73d-82426.jpg 1500w,\n/static/IMG_3396-369216b758c0815500af50dc3c15e73d-2b8b3.jpg 2544w\" sizes=\"(max-width: 2544px) 100vw, 2544px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n  <div class=\"col-7to9\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2280px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.37719298245614%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_8841-0cfc5145d4486e395e1723429d9528e8-38bf1.jpg\" srcset=\"/static/IMG_8841-0cfc5145d4486e395e1723429d9528e8-d4d08.jpg 750w,\n/static/IMG_8841-0cfc5145d4486e395e1723429d9528e8-03022.jpg 1500w,\n/static/IMG_8841-0cfc5145d4486e395e1723429d9528e8-38bf1.jpg 2280w\" sizes=\"(max-width: 2280px) 100vw, 2280px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n  <div class=\"col-10to12\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2200px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.3181818181818%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_8927-59a6b2960837ed59f2027ce252c70110-97768.jpg\" srcset=\"/static/IMG_8927-59a6b2960837ed59f2027ce252c70110-ea324.jpg 750w,\n/static/IMG_8927-59a6b2960837ed59f2027ce252c70110-d792e.jpg 1500w,\n/static/IMG_8927-59a6b2960837ed59f2027ce252c70110-97768.jpg 2200w\" sizes=\"(max-width: 2200px) 100vw, 2200px\">\n    </span>\n  </span>\n  \n      <figcaption></figcaption>\n    </figure>\n  </div>\n</div>\n<br>\n<br>\n<br>\n<h3>Entwurf «Classic»</h3>\n<p>Pascale, Natascha und Nicole haben eine sehr reduzierte und einfache Form für das Interface gewählt. Der Entwurf kann grafisch und puncto Interface mit wenigen Korrekturen zu einer guten Lösung ausgebaut werden.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-12\">\n    <h3>Prototype</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_0149-3d5eccf134e4f4ed94286d7d6933da61-84fcc.jpg\" srcset=\"/static/IMG_0149-3d5eccf134e4f4ed94286d7d6933da61-5424a.jpg 750w,\n/static/IMG_0149-3d5eccf134e4f4ed94286d7d6933da61-6baeb.jpg 1500w,\n/static/IMG_0149-3d5eccf134e4f4ed94286d7d6933da61-84fcc.jpg 3000w,\n/static/IMG_0149-3d5eccf134e4f4ed94286d7d6933da61-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Paper-Prototype ist noch in einem sehr rohen Zustand.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Aktuelle Zeit gleich ersichtlich.</li>\n      <li>Wenige Interface-Elemente die verwirren.</li>\n      <li>Einschr&#xE4;nkungen im Schienensystem gut ersichtlich.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Eine Auswahl der Zeit einige Stunden sp&#xE4;ter ist eher schwer m&#xF6;glich.</li>\n      <li>Die Aufteilung der zwei Spalten m&#xFC;sste man noch einmal pr&#xFC;fen.</li>\n      <li>Schrift ist eher klein.</li>\n    </ul>\n  </div>\n</div>\n<div class=\"wide-grid bg\">\n  <div class=\"col-12\">\n    <h3>Entwurf</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure class=\"border\">\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2464px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 178.24675324675323%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/gruppe_a_abgabe-ce704143be400c1d2d88cd5724d4ecf5-a222b.jpg\" srcset=\"/static/gruppe_a_abgabe-ce704143be400c1d2d88cd5724d4ecf5-0a152.jpg 750w,\n/static/gruppe_a_abgabe-ce704143be400c1d2d88cd5724d4ecf5-676bf.jpg 1500w,\n/static/gruppe_a_abgabe-ce704143be400c1d2d88cd5724d4ecf5-a222b.jpg 2464w\" sizes=\"(max-width: 2464px) 100vw, 2464px\">\n    </span>\n  </span>\n  \n      <figcaption>Der Entwurf ist leicht verst&#xE4;ndlich und auf das wesentliche reduziert.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Schnelle Stundenwahl</li>\n      <li>Klares Interface-Konzept</li>\n      <li>St&#xF6;rungen gut erkennbar</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Die Schriftgr&#xF6;sse der Z&#xFC;ge ist zu klein</li>\n      <li>Eine Zeitmarke, wo die aktuell Zeit gerade steht, w&#xFC;rde helfen</li>\n      <li>Funktion der grauen Fl&#xE4;che (links) m&#xFC;sste noch gepr&#xFC;ft werden.</li>\n      <li>Button (Haus- &amp; Rollsthul-Icon) haben die selbe Erscheinung wie Statuswahl (10 Uhr). Verschiedene Funktion, verschiedene Erscheinung.</li>\n      <li>Bei der Wahl der Uhrzeit links ist es eher schwer zu erkennen, dass es sich um Uhrzeiten handelt.</li>\n      <li>Die beiden Spalten sind eher eng beieinander.</li>\n    </ul>\n    <h4>&#xDC;bersicht</h4>\n    <p>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2605; &#x2013; Technische Realisierbarkeit<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Inhaltliche Struktur<br>\n      &#x2605;&#x2605;&#x2605;<span class=\"star half\">&#x2606;</span>&#x2606; &#x2013; Interaktion/F&#xFC;hrung<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Grafisches Konzept<br>\n    </p>\n  </div>\n</div>\n<br>\n<br>\n<br>\n<h3>Entwurf «Simple»</h3>\n<p>Wolfgang, Nils, Nadia und Lars haben einen Entwurf erarbeitet, bei dem die Wahl der Zeit möglichst einfach geschehen soll. Die Wahl ist mehrfach geführt (Stunde, Jetzt, Zeitwähler) und man müsste mit einer Testgruppe prüfen, ob dies verwirrend oder unterstützend ist.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-12\">\n    <h3>Prototype</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_6425-81f89cc4d6a59ee0c38216d1c0a4b218-84fcc.jpg\" srcset=\"/static/IMG_6425-81f89cc4d6a59ee0c38216d1c0a4b218-5424a.jpg 750w,\n/static/IMG_6425-81f89cc4d6a59ee0c38216d1c0a4b218-6baeb.jpg 1500w,\n/static/IMG_6425-81f89cc4d6a59ee0c38216d1c0a4b218-84fcc.jpg 3000w,\n/static/IMG_6425-81f89cc4d6a59ee0c38216d1c0a4b218-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Paper-Prototype zeigt, dass die Navigation durch die Zeit das Hauptthema ist.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Lesbarkeit des Panels gegeben. </li>\n      <li>Klarer Aufbau zwischen Navigation (Touch) und Informationsanzeige.</li>\n      <li>Scrolling in der Zeit klarer von Zeitwahl unterscheiden.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Uhr oben sehr pr&#xE4;sent.</li>\n      <li>Home-Button nicht vergessen.</li>\n      <li>Touch gr&#xF6;ssen nicht zu klein w&#xE4;hlen.</li>\n      <li>Dreifache Zeitwahl (Stunde, Jetzt, Zeitw&#xE4;hler) schl&#xFC;ssig? </li>\n    </ul>\n  </div>\n</div>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to12\">\n    <h3>Entwurf</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure class=\"border\">\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 178.26666666666668%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/gruppe_b_abgabe-cdb2d6b6591d87656f50c71107df9932-84fcc.jpg\" srcset=\"/static/gruppe_b_abgabe-cdb2d6b6591d87656f50c71107df9932-5424a.jpg 750w,\n/static/gruppe_b_abgabe-cdb2d6b6591d87656f50c71107df9932-6baeb.jpg 1500w,\n/static/gruppe_b_abgabe-cdb2d6b6591d87656f50c71107df9932-84fcc.jpg 3000w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Der Entwurf fokussiert auf die aktuellen Abfahrtszeiten und erm&#xF6;glicht eine gezielte Wahl der Anzeigezeit.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>    \n    <ul>\n      <li>Auffindbarkeit f&#xFC;r Z&#xFC;ge in den n&#xE4;chsten Minuten gegeben.</li>\n      <li>Klare Typografie und strukturierter Aufbau.</li>\n      <li>Vertraute Elemente vom Interface-Design wurden verwendet.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Die Schriftgr&#xF6;sse der Z&#xFC;ge ist zu klein</li>\n      <li>Es gibt Blau, was als Status benutzt wird (bei DE) aber auch als Button (Rollsthul-Icon und Pfeile). Verschiedene Funktion, verschiedene Erscheinung.</li>\n      <li>Der n&#xE4;chste abfahrende Zug ist ersichtlich anhand der &#xAB;Legende&#xBB;. Diese Markierung k&#xF6;nnte man so verst&#xE4;rken, dass klar wird alle &#xFC;ber der Legende sind bereits gefahren.</li>\n      <li>Zeichenerkl&#xE4;rung sollte allenfals bei den Z&#xFC;gen integriert werden (Informationserschliessung &#xFC;ber Touch)</li>\n    </ul>\n    <h4>&#xDC;bersicht</h4>\n    <p>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2605; &#x2013; Technische Realisierbarkeit<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Inhaltliche Struktur<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Interaktion/F&#xFC;hrung<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Grafisches Konzept<br>\n    </p>\n  </div>\n</div>\n<br>\n<br>\n<br>\n<h3>Entwurf «Slide»</h3>\n<p>Stephanie, Matthias und Marc haben einen Entwurf erarbeitet, in der die Zeitnavigation hinter die Information zurück tritt. Die ursprüngliche Idee, Vormittag und Nachmittag zu trennen, wurde im ausgearbeiteten Entwurf zu gunsten der Benutzerführung verworfen.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to12\">\n    <h3>Prototype</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_2905-27ce4d5ca48d35e0d765ebd6862c9bae-84fcc.jpg\" srcset=\"/static/IMG_2905-27ce4d5ca48d35e0d765ebd6862c9bae-5424a.jpg 750w,\n/static/IMG_2905-27ce4d5ca48d35e0d765ebd6862c9bae-6baeb.jpg 1500w,\n/static/IMG_2905-27ce4d5ca48d35e0d765ebd6862c9bae-84fcc.jpg 3000w,\n/static/IMG_2905-27ce4d5ca48d35e0d765ebd6862c9bae-4b69e.jpg 4032w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Der Paper-Prototype hat noch eine Aufteilung des Tages (Zeit) in zwei Spalten.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Grosse Typografie, Lesbarkeit</li>\n      <li>Sprachwal bedacht. Aber wirklich n&#xF6;tig?</li>\n      <li>Information hat die h&#xF6;here Priorit&#xE4;t als Interaktion. Passt wohl f&#xFC;r viele, die den Zug in den n&#xE4;chsten Minuten suchen.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Ist die Aufteilung Vormittag/Nachmittag schl&#xFC;ssig?</li>\n      <li>Braucht es die Font-Skallierung? Oder nicht besser eine &#xAB;gute&#xBB; Gr&#xF6;sse?</li>\n      <li>Um eine Uhrzeit in der Zukunft zu w&#xE4;hlen, muss unter umst&#xE4;nden sehr oft getoucht werden.</li>\n    </ul>\n  </div>\n</div>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to12\">\n    <h3>Entwurf</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure class=\"border\">\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 2460px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 178.45528455284554%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/gruppe_c_abgabe-92f79cde1b8eb62f0738b79cb5271147-06417.jpg\" srcset=\"/static/gruppe_c_abgabe-92f79cde1b8eb62f0738b79cb5271147-ac40b.jpg 750w,\n/static/gruppe_c_abgabe-92f79cde1b8eb62f0738b79cb5271147-79716.jpg 1500w,\n/static/gruppe_c_abgabe-92f79cde1b8eb62f0738b79cb5271147-06417.jpg 2460w\" sizes=\"(max-width: 2460px) 100vw, 2460px\">\n    </span>\n  </span>\n  \n      <figcaption>Der Entwurf kann mit wenigen Korrekturen zu einem tollen Panel ausgebaut werden.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Eine Zeitmarke, wo die aktuell Zeit gerade steht, w&#xFC;rde helfen</li>\n      <li>Vertraute Elemente vom Interface-Design wurden verwendet. </li>\n      <li>Informationserschliessung &#xFC;ber +-Symbol erweiterbar.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Die Schriftgr&#xF6;sse der Z&#xFC;ge ist zu klein</li>\n      <li>Die Zeitwahl ist in der H&#xF6;he verteilt; dadurch erschwert man die Bedienung f&#xFC;r kleinere Menschen.</li>\n      <li>F&#xFC;r die Zeitwahl abweichend von &#xAB;Jetzt&#xBB; m&#xFC;ssen unter umst&#xE4;nden viele Touches gemacht werden.</li>\n      <li>Sprachwahl und Schriftgr&#xF6;sse f&#xFC;r kleinere Menschen nicht zu erreichen. &#xAB;Rollsthul-Modus&#xBB; wohl f&#xFC;r viele Personen die einzige M&#xF6;glichkeit.</li>\n    </ul>\n    <h4>&#xDC;bersicht</h4>\n    <p>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2605; &#x2013; Technische Realisierbarkeit<br>\n      &#x2605;&#x2605;&#x2605;&#x2606;&#x2606; &#x2013; Inhaltliche Struktur<br>\n      &#x2605;&#x2605;&#x2605;<span class=\"star half\">&#x2606;</span>&#x2606; &#x2013; Interaktion/F&#xFC;hrung<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2606; &#x2013; Grafisches Konzept<br>\n    </p>\n  </div>\n</div>\n<br>\n<br>\n<br>\n<h3>Entwurf «One-Touch»</h3>\n<p>Tioe, Severin und Marius haben durch eine geschickte Aufteilung des Tages eine sehr klare Führung über die Zeit erreicht. Die Anzeige der Information wurde an die anderen Info-Screens im Zugsverkehr angelehnt.</p>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to12\">\n    <h3>Prototype</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure>\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/IMG_0011-36824bc9e986dccfb88bb840ed687b0c-84fcc.jpg\" srcset=\"/static/IMG_0011-36824bc9e986dccfb88bb840ed687b0c-5424a.jpg 750w,\n/static/IMG_0011-36824bc9e986dccfb88bb840ed687b0c-6baeb.jpg 1500w,\n/static/IMG_0011-36824bc9e986dccfb88bb840ed687b0c-84fcc.jpg 3000w,\n/static/IMG_0011-36824bc9e986dccfb88bb840ed687b0c-a0acb.jpg 3024w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Die Aufteilung des Tages in vier &#xAB;Tageszeiten&#xBB; ist im Paper-Prototye bereits ersichtlich.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Klare Typografie und strukturierter Aufbau.</li>\n      <li>Klares Interface-Konzept</li>\n      <li>Schnelle Stundenwahl</li>\n      <li>Die 4 Bl&#xF6;cke f&#xFC;r die Tageszeiten funktionieren sehr gut!</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Eine Zeitmarke, wo die aktuell Zeit gerade steht, w&#xFC;rde helfen.</li>\n      <li>K&#xF6;nnen vielleicht noch mehr Informationen erschlossen werden?</li>\n    </ul>\n  </div>\n</div>\n<div class=\"wide-grid bg\">\n  <div class=\"col-1to12\">\n    <h3>Entwurf</h3>\n  </div>\n  <div class=\"col-1to7\">\n    <figure class=\"border\">\n      \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 3000px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 178.26666666666668%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"test\" title src=\"/static/gruppe_d_abgabe-16652b92ddccb8cda4e9fe4afd27ebe6-84fcc.jpg\" srcset=\"/static/gruppe_d_abgabe-16652b92ddccb8cda4e9fe4afd27ebe6-5424a.jpg 750w,\n/static/gruppe_d_abgabe-16652b92ddccb8cda4e9fe4afd27ebe6-6baeb.jpg 1500w,\n/static/gruppe_d_abgabe-16652b92ddccb8cda4e9fe4afd27ebe6-84fcc.jpg 3000w\" sizes=\"(max-width: 3000px) 100vw, 3000px\">\n    </span>\n  </span>\n  \n      <figcaption>Der fertige Entwruf f&#xFC;hrt den Benutzer &#xFC;ber die klare Zeitnavigation schnell zu den gew&#xFC;nschten Abfahrtszeiten.</figcaption>\n    </figure>\n  </div>\n  <div class=\"col-8to12\">\n    <h4>Positives</h4>\n    <ul>\n      <li>Typografie der Z&#xFC;ge eher klein.</li>\n      <li>Information und Interaktion sind optisch gut getrennt.</li>\n      <li>Es gibt ein Haupt-Interface (Zeitwahl) und dieses wurde sehr klar gestaltet.</li>\n      <li>Das Blau f&#xFC;r Info-Displays der sonstigen Anzeigen im Bahnhof wurde &#xFC;bernommen.</li>\n    </ul>\n    <h4>Inputs/Verbessern</h4>\n    <ul>\n      <li>Die Tageszeiten sind durch die starke Block-Bildung im Entwurf fast besser erkennbar.</li>\n      <li>Zeitmarke st&#xF6;rt den sonst sehr klaren Aufbau. Kann aber leicht korrigiert werden.</li>\n      <li>Scroll-Up und -Down sind nun sehr weit auseinander. Grosse Bewegung und hoch oben.</li>\n    </ul>\n    <h4>&#xDC;bersicht</h4>\n    <p>\n      &#x2605;&#x2605;&#x2605;&#x2605;&#x2605; &#x2013; Technische Realisierbarkeit<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;<span class=\"star half\">&#x2606;</span> &#x2013; Inhaltliche Struktur<br>\n      &#x2605;&#x2605;&#x2605;&#x2605;<span class=\"star half\">&#x2606;</span> &#x2013; Interaktion/F&#xFC;hrung<br>\n      &#x2605;&#x2605;&#x2605;<span class=\"star half\">&#x2606;</span>&#x2606; &#x2013; Grafisches Konzept<br>\n    </p>\n  </div>\n</div>","fields":{"tagSlugs":["/tags/case-study/","/tags/interface/","/tags/typografie/"]},"frontmatter":{"title":"Fahrplanauskunft – Redesign Touch Panel","author":"Jonas Shriqui & Stefan Huber","category":"Case Study","tags":["Case Study","Interface","Typografie"],"date":"2017-11-11","description":"In einem eintägigen Workshop wurde ein bestehendes Touch Panel für die Fahrplanauskunft am Zürich Hauptbahnhof überarbeitet und die bestehende Mängel von vier Teams analysiert und neue Lösungen vorgeschlagen.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-f6429ff619592e8269992f879e064f78-ef3a8.png","srcSet":"/static/header-f6429ff619592e8269992f879e064f78-e0a0e.png 750w,\n/static/header-f6429ff619592e8269992f879e064f78-0bb6d.png 1500w,\n/static/header-f6429ff619592e8269992f879e064f78-ef3a8.png 3000w","sizes":"(max-width: 3000px) 100vw, 3000px"}}}},"wordCount":{"words":733}}},"pathContext":{"slug":"/posts/case-study-panel-sbb/"}}

/***/ })

